import { clone, sortBy, valuesOfKey, getRange, each, hasKey, groupBy, isEmpty } from '@antv/util'; //  range

function getBinKey(value, binWidth) {
  var index = Math.floor(value / binWidth);
  return [binWidth * index, binWidth * (index + 1)];
} //  sturges 


function sturges(values) {
  return Math.ceil(Math.log(values.length) / Math.LN2) + 1;
}
/**
 * 
 * @param data
 * @param binField
 * @param binWidth
 * @param binNumber
 * @param stackField
 */


export function binHistogram(data, binField, binWidth, binNumber, stackField) {
  var originData_copy = clone(data); //  binField 

  sortBy(originData_copy, binField); //  binField  range

  var values = valuesOfKey(originData_copy, binField);
  var range = getRange(values);
  var rangeWidth = range.max - range.min; //  binWidth binNumber  binWidth 

  var _binWidth = binWidth;

  if (!binWidth && binNumber) {
    _binWidth = rangeWidth / binNumber;
  } //  binWidth  binNumber  Sturges formula  binWidth


  if (!binWidth && !binNumber) {
    var _defaultBinNumber = sturges(values);

    _binWidth = rangeWidth / _defaultBinNumber;
  } //  key - StatisticData 


  var bins = {};
  var groups = groupBy(originData_copy, stackField); //  stackField 

  if (isEmpty(groups)) {
    each(originData_copy, function (data) {
      var value = data[binField];
      var bin = getBinKey(value, _binWidth);
      var binKey = bin[0] + "-" + bin[1];

      if (!hasKey(bins, binKey)) {
        bins[binKey] = {
          range: bin,
          count: 0
        };
      }

      bins[binKey].count += 1;
    });
  } else {
    Object.keys(groups).forEach(function (groupKey) {
      each(groups[groupKey], function (data) {
        var value = data[binField];
        var bin = getBinKey(value, _binWidth);
        var binKey = bin[0] + "-" + bin[1];
        var groupKeyBinKey = binKey + "-" + groupKey;

        if (!hasKey(bins, groupKeyBinKey)) {
          bins[groupKeyBinKey] = {
            range: bin,
            count: 0
          };
          bins[groupKeyBinKey][stackField] = groupKey;
        }

        bins[groupKeyBinKey].count += 1;
      });
    });
  } //  plotData 


  var plotData = [];
  each(bins, function (bin) {
    plotData.push(bin);
  });
  return plotData;
}