/**
 *  syncViewPadding 
 * @param chart
 * @param views
 * @param PC: PaddingCalCtor
 */
export function defaultSyncViewPadding(chart, views, PC) {
  var syncPadding = PC.instance(); //  view  autoPadding 

  views.forEach(function (v) {
    v.autoPadding = syncPadding.max(v.autoPadding.getPadding());
  });
}