import { reduce } from '@antv/util';
/**
 *  Chart  type  geometry
 * @param chart
 * @param type
 */

export function findGeometry(chart, type) {
  return chart.geometries.find(function (g) {
    return g.type === type;
  });
}
/**
 *  Chart   elements
 */

export function getAllElements(chart) {
  return reduce(chart.geometries, function (r, geometry) {
    return r.concat(geometry.elements);
  }, []);
}