import { get, isNumber } from '@antv/util';
/**
 * 
 * @param data
 * @param field
 */

export function adjustYMetaByZero(data, field) {
  // 
  var numberData = data.filter(function (datum) {
    var v = get(datum, [field]);
    return isNumber(v) && !isNaN(v);
  });
  var gtZero = numberData.every(function (datum) {
    return get(datum, [field]) >= 0;
  });
  var ltZero = numberData.every(function (datum) {
    return get(datum, [field]) <= 0;
  }); //  { min: 0, max: undefined }  update({ max: 0 })  { min: 0, max: 0 }

  if (gtZero) {
    return {
      min: 0
    };
  }

  if (ltZero) {
    return {
      max: 0
    };
  }

  return {};
}
/**
 * 
 * @param data
 * @param sourceField
 * @param targetField
 * @param weightField
 */

export function transformDataToNodeLinkData(data, sourceField, targetField, weightField) {
  if (!Array.isArray(data)) {
    return {
      nodes: [],
      links: []
    };
  } //   const nodes = [];


  var links = []; // 

  var nodesMap = {};
  var nodesIndex = -1; //  chord layout 

  data.forEach(function (datum) {
    var source = datum[sourceField];
    var target = datum[targetField];
    var weight = datum[weightField]; // source node

    if (!nodesMap[source]) {
      nodesMap[source] = {
        id: ++nodesIndex,
        name: source
      };
    }

    if (!nodesMap[target]) {
      nodesMap[target] = {
        id: ++nodesIndex,
        name: target
      };
    } // links


    links.push({
      source: nodesMap[source].id,
      target: nodesMap[target].id,
      // sourceName: source,
      // targetName: target,
      value: weight
    });
  });
  return {
    nodes: Object.values(nodesMap),
    links: links
  };
}